import {
  isChatFileContext,
  isChatWatchlistContext,
  isPromptLibraryContext,
} from "@rp/react/common/hooks"
import { ChatInteractionType } from "@rp/react/tanstack-api/chat"

const getInteractionOrigin = (context: unknown, interactionOrigin: ChatInteractionType) => {
  if (isChatFileContext(context)) {
    return "files" as const
  }

  if (isChatWatchlistContext(context)) {
    return "watchlist" as const
  }

  if (isPromptLibraryContext(context)) {
    return "suggestedPrompts" as const
  }

  return interactionOrigin
}

export { getInteractionOrigin }
